body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

html {
  height: 100%; }

body {
  height: 100%;
  overflow: hidden; }

#root {
  height: 100%; }

.navbar-expand-lg .navbar-nav .header-link {
  padding: 0.5rem 1rem;
  color: rgba(255, 255, 255, 0.5); }
  .navbar-expand-lg .navbar-nav .header-link:hover {
    color: rgba(255, 255, 255, 0.75);
    text-decoration: none; }

.navbar-nav .header-link {
  padding-right: 0;
  padding-left: 0; }

.footer {
  margin-top: auto !important;
  height: 20px;
  background-color: #343a40;
  z-index: 1; }

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #282c34; }

.dashboard.light {
  background: none;
  background-image: radial-gradient(circle at top left, white, #282c34); }

.dashboard-content {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  font-size: calc(10px + 2vmin);
  color: white; }
  .dashboard-content .dashboard-side-panel {
    position: absolute;
    width: 100%;
    display: flex;
    height: 100%; }
  .dashboard-content .dashboard-side {
    width: 35%;
    overflow: auto;
    margin-bottom: 70px;
    position: absolute; }
  .dashboard-content .dashboard-central {
    align-items: center;
    padding: 2% 20%;
    width: 100%;
    height: 100%; }

.dashboard-content .dashboard-left {
  direction: rtl; }
  .dashboard-content .dashboard-left .moving-pane {
    direction: ltr; }

.moving-pane-picture .arrow:hover {
  color: rgba(255, 255, 255, 0.2); }

.moving-pane-picture-hidden .arrow:hover {
  color: rgba(255, 255, 255, 0.2); }

.dashboard-content .dashboard-right {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin-left: auto; }
  .dashboard-content .dashboard-right .moving-pane {
    flex-direction: row-reverse; }
  .dashboard-content .dashboard-right .moving-pane-picture .arrow {
    transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
    margin-left: auto; }
  .dashboard-content .dashboard-right .moving-pane-picture-hidden .arrow {
    transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    margin-left: auto; }

.moving-pane {
  display: flex;
  flex-direction: row;
  height: 200px;
  width: 100%;
  padding: 5px 0 0 5px; }
  .moving-pane .moving-pane-content {
    background-color: black;
    opacity: 0.5;
    width: 80%;
    transition: 0.8s;
    padding: 10px; }
  .moving-pane .moving-pane-content-hidden {
    width: 0;
    height: 100%;
    overflow-x: hidden;
    transition: 0.8s; }
  .moving-pane .moving-pane-picture {
    cursor: pointer;
    transition: margin-left 0.8s;
    height: 100%;
    width: 20%;
    display: flex; }
    .moving-pane .moving-pane-picture .arrow {
      align-self: center;
      transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
      overflow-x: hidden;
      transition: 0.8s; }
  .moving-pane .moving-pane-picture-hidden {
    display: flex;
    cursor: pointer;
    transition: margin-left 0.8s;
    height: 100%;
    width: 20%; }
    .moving-pane .moving-pane-picture-hidden .arrow {
      align-self: center;
      transform: rotate(-180deg);
      -ms-transform: rotate(-180deg);
      -webkit-transform: rotate(-180deg);
      overflow-x: hidden;
      transition: 0.8s; }

.MuiDialogContent-root {
  background-image: radial-gradient(circle at bottom right, #5b1f6b, #160f30); }

.company-title {
  font-size: 40px;
  color: #5b1f6b;
  font-weight: bold; }

.company-title-company {
  font-size: 30px;
  font-weight: bold;
  color: #8a5b97; }

.company-separator {
  background: rgba(0, 0, 0, 0.3);
  height: 5px;
  margin: 10px; }

.company-input {
  padding: 10px 0;
  width: 100%; }
  .company-input input {
    width: 100%; }

.field-company {
  width: 100%;
  margin-top: 10px; }
